//  Input fields with animation
.input-wrapper {
   position: relative;
   height: 50px;
   overflow: hidden;
   input {
       width: 100%;
       height: 100%;
       color: #595f6e;
       padding-top: 25px;
       border: none;
       outline: none; 
   }
   label {
       position: absolute;
       bottom: 0px;
       left: 0%;
       width: 100%;
       height: 100%;
       pointer-events: none;
       border-bottom: 1px solid black;
       &::after {
           content: "";
           position: absolute;
           left: 0px;
           bottom: -1px;
           height: 100%;
           width: 100%;
           border-bottom: 3px solid#2f3640;
           transform: translateX(-100%);
           transition: transform 0.3s ease;
       }
       .label-text {
           position: absolute;
           bottom: 5px;
           left: 0px;
           transition: all 0.3s ease;
        }
   }
   .add-description, .add-value {
       font-size: 14px;
       padding: 12px 15px;
    }
}

.input-wrapper input:focus + .label-name .label-text, .input-wrapper input:not(:placeholder-shown) + .label-name .label-text {
   transform: translateY(-150%);
   font-size: 14px;
   color: #5fa8d3;
   color: #2f3640;
}
.input-wrapper input:focus + .label-name::after, .input-wrapper input:not(:placeholder-shown) + .label-name::after {
   transform: translateX(0%);
}