.modal-overlay {
   position: fixed;
   width: 100vw;
   height: 100vh;
   top: 0;
   left: 0;
   background: rgba(0, 0, 0, 0.5);
   display: flex;
   align-items: center;
   justify-content: center;
   .modal-window {
      max-width: 500px;
      background: #fff;
      border: 1px solid #fff;
      font-size: 22px;
      box-shadow: 0 0 15px rgba(0, 0, 0, 0.25);
      .modal-titlebar {
         height: 40px;
         background: #333333;
         display: flex;
         align-items: center;
         justify-content: space-between;
         .modal-title {
            margin-left: 15px;
            font-weight: bold;
            color: #eee;
         }
         .modal-close {
            width: 40px;
            height: 40px;
            outline: none;
            border: none;
            background: transparent;
            color: #eee;
            cursor: pointer;
            &:active {
               transform: scale(0.85);
            }
         }
      }
      .modal-content {
         padding: 15px;
         font-size: 0.9em;
      }
   }
}

